import Vue from 'vue'
import Router from 'vue-router'

import loginRegister from './login-register'
import product from './product'
import personalCenter from './personalCenter'
import claim from './claim'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'Home',
      component: resolve => require(['@/views/Home.vue'], resolve),
      meta: {
        title: '首页',
        requireAuth: false,
        isFoot: true
      }
    },
    {
      path: '/product',
      name: 'Product',
      redirect: '/product/list',
      component: resolve => require(['@/views/ProductList.vue'], resolve),
      meta: {
        title: '产品列表页',
        requireAuth: false,
        isFoot: true
      },
      children: [
        ...product
      ]
    },
    {
      path: '/personalCenter',
      name: 'PersonalCenter',
      component: resolve => require(['@/views/PersonalCenter.vue'], resolve),
      meta: {
        title: '个人中心',
        requireAuth: false,
        isFoot: true
      }
    },
    {
      path: '/smsLogin',
      name: 'SmsLogin',
      component: resolve => require(['@/components/login/smsLogin.vue'], resolve),
      meta: {
        title: '短信登录'
      }
    },
    ...loginRegister,
    ...personalCenter,
    ...claim,
    {
      path: '/insuranceResult/:orderSn',
      name: 'Success',
      component: resolve => require(['@/views/InsuredResult.vue'], resolve),
      meta: {
        title: '投保结果',
        requireAuth: true
      }
    },
    {
      path: '*',
      component: resolve => require(['@/views/NotFound.vue'], resolve),
      meta: {
        title: '功能开发中...'
      }

    }
  ],
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return {x: 0, y: 0}
    }
  }
})
