const claimApply = resolve => require(['@/components/claim/claimApply'], resolve)
const claimList = resolve => require(['@/components/claim/myClaimList'], resolve)
const ClaimUpload = resolve => require(['@/components/claim/upload'], resolve)
const ClaimUploadSuccess = resolve => require(['@/components/claim/success'], resolve)
const claimDetail = resolve => require(['@/components/claim/medicalSettle'], resolve)
export default [

  {
    path: '/claimList',
    name: 'ClaimList',
    component: claimList,
    meta: {
      title: '理赔列表',
      requireAuth: true
    },
    children: [
      {
        path: 'detail/:id',
        name: 'ClaimDetail',
        component: claimDetail,
        meta: {
          title: '理赔详情',
          requireAuth: true
        }
      },
      {
        path: 'claimApply',
        name: 'ClaimApply',
        component: claimApply,
        meta: {
          title: '理赔申请',
          requireAuth: true
        }
      }
    ]
  },
  {
    path: '/claimUpload',
    component: ClaimUpload,
    name: 'claimUpload',
    meta: {
      title: '资料提交',
      requireAuth: false,
      isFoot: false
    }
  },
	{
    path: '/claimModify',
    component: ClaimUpload,
    name: 'claimModify',
    meta: {
      title: '资料修改',
      requireAuth: false,
      isFoot: false
    }
  },
  {
    path: '/claimUpload/success/:data',
    component: ClaimUploadSuccess,
    name: 'ClaimUploadSuccess',
    meta: {
      title: '资料提交',
      requireAuth: false,
      isFoot: false
    }
  }
]
